.userList {
  flex: 4;
}

.col-md-4 {
  margin-bottom: 5px;
  width: 300px;
}

.form-control {
  padding: 5px;
  width: 100%;
  height: 25px;
  border-radius: 4px;
}

.userListUser {
  display: flex;
  align-items: center;
}

.userListImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.userListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb077;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}

.userListDelete {
  color: red;
  cursor: pointer;
}

.tableContainer {
  padding: 10px 20px;
}

.userAddButton {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  border-radius: 5px;
  cursor: pointer;
  color: white;
  font-size: 16px;
}

.wrapper{
  display: flex;
  justify-content: space-between;
}
